.cont {
  min-width: 550px;
  min-height: 450px;
  display: flex;
  max-width: 750px;
  flex-direction: column;
  gap: 30px;
  box-shadow: 1px 1px 4px rgba(0,0,0,0.25);
  padding-top: 10px;
  height: fit-content;
  margin:0 auto;
}

.header {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  border-bottom: 1px solid rgba(0,0,0,0.1);
}

.header::-webkit-scrollbar {
  height: 7px;
  /* border-radius: 20px; */
  /* cursor: pointer; */
}


.header::-webkit-scrollbar-thumb {
  
  border-radius: 20px;
  background-color: #c7c7c7;
  

}



.section {
  padding: 10px;
  border-bottom: 2px solid transparent;
  font-weight: 500;
  font-size: 1rem;
  white-space: nowrap;
  cursor: pointer;
}

.active {
  border-bottom: 2px solid #d9f055;
  color: #d9f055;
}

.body {
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px; 
  padding-top: 0;
}


.detail {
  display: flex;
  flex-direction: column;
  gap:14px;
}

.detail .row{
  display: flex;
  /* margin: 20px; */
  gap: 20px;
}

.detail .column {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.detail .row >div{
  flex: 1;
}

.chips{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  /* padding: 4px 8px; */
  gap: 20px;
}

.chips .new{
  color: #d6ca00;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 1.25rem;
  cursor: pointer;
}

.chip {
  display: flex;
  gap:5px;
  align-items: center;
  padding: 4px 8px;
  background-color: #808080;
  border-radius: 20px;
  cursor: default;
  
}

.chip p{
  font-weight: 500px;
  font-size: 1.25rem;
  color: #fff;
}

.chip svg {
  color: #fff;
  height: 18px;
  width: 18px;
  cursor: pointer;
}

.chips .active {
  background-color: #d6ca00;
}

.body button {

  width:fit-content;
  padding: 8px 16px;
  border-radius: 5px;
  background-color: #d6ca00;
  color:#fff;
  border: none;
  cursor: pointer;
  outline: none;
  font-weight: 500px;
  font-size: 1rem;
  letter-spacing: 1px;
  display: flex;
  gap: 10px;
  align-items: center;
  /* cursor: pointer; */
  transition: 200ms;
}

.body button:active {
  transform: translateY(4px);
}

